import { Trade } from '@udegoswap/sdk';
import flatMap from 'lodash/flatMap';
import { useMemo } from 'react';
import { isTradeBetter } from '../utils/trades';
import { useUserSingleHopOnly } from '../state/user/hooks';
import useActiveWeb3React from "./useWeb3";
import { BASES_TO_CHECK_TRADES_AGAINST, CUSTOM_BASES, BETTER_TRADE_LESS_HOPS_THRESHOLD, ADDITIONAL_BASES, } from '../config/constants/index';
import { PairState, usePairs } from './usePairs';
import { wrappedCurrency } from '../utils/wrappedCurrency';
import { useUnsupportedTokens } from './Tokens';

function useAllCommonPairs(currencyA, currencyB) {
    const { chainId } = useActiveWeb3React();
    const [tokenA, tokenB] = chainId
        ? [wrappedCurrency(currencyA, chainId), wrappedCurrency(currencyB, chainId)]
        : [undefined, undefined];

    const bases = useMemo(() => {
        if (!chainId)
            return [];
        // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
        const common = BASES_TO_CHECK_TRADES_AGAINST[chainId] ?? []
        const additionalA = tokenA ? ADDITIONAL_BASES[chainId]?.[tokenA.address] ?? [] : []
        const additionalB = tokenB ? ADDITIONAL_BASES[chainId]?.[tokenB.address] ?? [] : []
        return [...common, ...additionalA, ...additionalB];
    }, [chainId, tokenA, tokenB]);

    const basePairs = useMemo(() => flatMap(bases, (base) => bases.map((otherBase) => [base, otherBase])), [bases]);
    const allPairCombinations = useMemo(() => tokenA && tokenB
        ? [
            // the direct pair
            [tokenA, tokenB],
            // token A against all bases
            ...bases.map((base) => [tokenA, base]),
            // token B against all bases
            ...bases.map((base) => [tokenB, base]),
            // each base against all bases
            ...basePairs,
        ]
            .filter((tokens) => Boolean(tokens[0] && tokens[1]))
            .filter(([t0, t1]) => t0.address !== t1.address)
            .filter(([tokenA_, tokenB_]) => {
            if (!chainId)
                return true;
            const customBases = CUSTOM_BASES[chainId];
            // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
            const customBasesA = customBases === null || customBases === undefined ? undefined : customBases[tokenA_.address];
            // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
            const customBasesB = customBases === null || customBases === undefined ? undefined : customBases[tokenB_.address];
            if (!customBasesA && !customBasesB)
                return true;
            if (customBasesA && !customBasesA.find((base) => tokenB_.equals(base)))
                return false;
            if (customBasesB && !customBasesB.find((base) => tokenA_.equals(base)))
                return false;
            return true;
        })
        : [], [tokenA, tokenB, bases, basePairs, chainId]);
    const allPairs = usePairs(allPairCombinations);
    // only pass along valid pairs, non-duplicated pairs
    return useMemo(() => Object.values(allPairs
        // filter out invalid pairs
        .filter((result) => Boolean(result[0] === PairState.EXISTS && result[1]))
        // filter out duplicated pairs
        .reduce((memo, [, curr]) => {
        let _a;
            // eslint-disable-next-line no-cond-assign,no-void
        memo[curr.liquidityToken.address] = (_a = memo[curr.liquidityToken.address]) !== null && _a !== undefined ? _a : curr;
        return memo;
    }, {})), [allPairs]);
}
const MAX_HOPS = 3;
/**
 * Returns the best trade for the exact amount of tokens in to the given token out
 */
// eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
export function useTradeExactIn(currencyAmountIn, currencyOut) {
    // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
    const allowedPairs = useAllCommonPairs(currencyAmountIn === null || currencyAmountIn === undefined ? undefined : currencyAmountIn.currency, currencyOut);
    const [singleHopOnly] = useUserSingleHopOnly();
    // console.log(allowedPairs)

    return useMemo(() => {
        if (currencyAmountIn && currencyOut && allowedPairs.length > 0) {
            if (singleHopOnly) {
                // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
                return Trade.bestTradeExactIn(allowedPairs, currencyAmountIn, currencyOut, { maxHops: 1, maxNumResults: 1 })[0] ?? null
            }
            // search through trades with varying hops, find best trade out of them
            let bestTradeSoFar = null;
            // eslint-disable-next-line no-plusplus
            for (let i = 1; i <= MAX_HOPS; i++) {
                // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
                const currentTrade = Trade.bestTradeExactIn(allowedPairs, currencyAmountIn, currencyOut, { maxHops: i, maxNumResults: 1 })[0] ?? null
                // if current trade is best yet, save it
                if (isTradeBetter(bestTradeSoFar, currentTrade, BETTER_TRADE_LESS_HOPS_THRESHOLD)) {
                    bestTradeSoFar = currentTrade;
                }
            }
            return bestTradeSoFar;
        }
        return null;
    }, [allowedPairs, currencyAmountIn, currencyOut, singleHopOnly]);
}
/**
 * Returns the best trade for the token in to the exact amount of token out
 */

export function useTradeExactOut(currencyIn, currencyAmountOut) {
    // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
    const allowedPairs = useAllCommonPairs(currencyIn, currencyAmountOut === null || currencyAmountOut === undefined ? undefined : currencyAmountOut.currency);
    const [singleHopOnly] = useUserSingleHopOnly();
    return useMemo(() => {
        if (currencyIn && currencyAmountOut && allowedPairs.length > 0) {
            if (singleHopOnly) {
                // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
                return Trade.bestTradeExactOut(allowedPairs, currencyIn, currencyAmountOut, { maxHops: 1, maxNumResults: 1 })[0] ?? null
            }
            // search through trades with varying hops, find best trade out of them
            let bestTradeSoFar = null;
            // eslint-disable-next-line no-plusplus
            for (let i = 1; i <= MAX_HOPS; i++) {
                // eslint-disable-next-line no-cond-assign,no-void,prefer-destructuring
                const currentTrade = Trade.bestTradeExactOut(allowedPairs, currencyIn, currencyAmountOut, { maxHops: i, maxNumResults: 1 })[0] ?? null
                if (isTradeBetter(bestTradeSoFar, currentTrade, BETTER_TRADE_LESS_HOPS_THRESHOLD)) {
                    bestTradeSoFar = currentTrade;
                }
            }
            return bestTradeSoFar;
        }
        return null;
    }, [currencyIn, currencyAmountOut, allowedPairs, singleHopOnly]);
}
export function useIsTransactionUnsupported(currencyIn, currencyOut) {
    const unsupportedTokens = useUnsupportedTokens();
    const { chainId } = useActiveWeb3React();
    const tokenIn = wrappedCurrency(currencyIn, chainId);
    const tokenOut = wrappedCurrency(currencyOut, chainId);
    // if unsupported list loaded & either token on list, mark as unsupported
    if (unsupportedTokens) {
        if (tokenIn && Object.keys(unsupportedTokens).includes(tokenIn.address)) {
            return true;
        }
        if (tokenOut && Object.keys(unsupportedTokens).includes(tokenOut.address)) {
            return true;
        }
    }
    return false;
}