import { MaxUint256 } from '@ethersproject/constants';
import { ETHER } from '@udegoswap/sdk';
import { useCallback, useMemo } from 'react';
import useActiveWeb3React from "./useWeb3";
import { PC_ROUTER_ADDRESS } from '../config/config';
import useTokenAllowance from './useTokenAllowance';
import { Field } from '../state/swap/actions';
import { useTransactionAdder, useHasPendingApproval } from '../state/transactions/hooks';
import { computeSlippageAdjustedAmounts } from '../utils/prices';
import { calculateGasMargin } from '../utils';
import { useTokenContract } from './useContract';
import { useCallWithGasPrice } from './useCallWithGasPrice';
import {TokenAmount} from "@udegoswap/sdk";

export const ApprovalState = {
    "UNKNOWN": 0,
    "NOT_APPROVED": 1,
    "PENDING": 2,
    "APPROVED": 3
}

// returns a variable indicating the state of the approval and a function which approves if necessary or early returns
export function useApproveCallback(amountToApprove, spender) {
    const { account } = useActiveWeb3React();
    const { callWithGasPrice } = useCallWithGasPrice();
    const token = amountToApprove instanceof TokenAmount ? amountToApprove.token : undefined;
    const currentAllowance = useTokenAllowance(token, account !== null && account !== undefined ? account : undefined, spender);
    const pendingApproval = useHasPendingApproval(token === null || token === undefined ? undefined : token.address, spender);
    // check the current approval status
    const approvalState = useMemo(() => {
        if (!amountToApprove || !spender)
            return ApprovalState.UNKNOWN;
        if (amountToApprove.currency === ETHER)
            return ApprovalState.APPROVED;
        // we might not have enough data to know whether or not we need to approve
        if (!currentAllowance)
            return ApprovalState.UNKNOWN;

        // amountToApprove will be defined if currentAllowance is
        // eslint-disable-next-line no-nested-ternary
        return currentAllowance.lessThan(amountToApprove)
            ? pendingApproval
                ? ApprovalState.PENDING
                : ApprovalState.NOT_APPROVED
            : ApprovalState.APPROVED;
    }, [amountToApprove, currentAllowance, pendingApproval, spender]);
    const tokenContract = useTokenContract(token === null || token === undefined ? undefined : token.address);
    const addTransaction = useTransactionAdder();
    const approve = useCallback(async () => {
        if (approvalState !== ApprovalState.NOT_APPROVED) {
            console.error('approve was called unnecessarily');
            return;
        }
        if (!token) {
            console.error('no token');
            return;
        }
        if (!tokenContract) {
            console.error('tokenContract is null');
            return;
        }
        if (!amountToApprove) {
            console.error('missing amount to approve');
            return;
        }
        if (!spender) {
            console.error('no spender');
            return;
        }
        let useExact = false;
        const estimatedGas = await tokenContract.estimateGas.approve(spender, MaxUint256).catch(() => {
            // general fallback for tokens who restrict approval amounts
            useExact = true;
            return tokenContract.estimateGas.approve(spender, amountToApprove.raw.toString());
        });
        // eslint-disable-next-line consistent-return
        return callWithGasPrice(tokenContract, 'approve', [spender, useExact ? amountToApprove.raw.toString() : MaxUint256], {
            gasLimit: calculateGasMargin(estimatedGas),
        })
            .then((response) => {
            addTransaction(response, {
                summary: `Approve ${amountToApprove.currency.symbol}`,
                approval: { tokenAddress: token.address, spender },
            });
        })
            .catch((error) => {
            console.error('Failed to approve token', error);
            throw error;
        });
    }, [approvalState, token, tokenContract, amountToApprove, spender, addTransaction, callWithGasPrice]);
    return [approvalState, approve];
}
// wraps useApproveCallback in the context of a swap
export function useApproveCallbackFromTrade(trade, allowedSlippage = 0) {
    const amountToApprove = useMemo(() => (trade ? computeSlippageAdjustedAmounts(trade, allowedSlippage)[Field.INPUT] : undefined), [trade, allowedSlippage]);
    return useApproveCallback(amountToApprove, PC_ROUTER_ADDRESS);
}

export function useApproveCallbackForRemove(pairToApprove, spender) {
    const { account } = useActiveWeb3React();
    const { callWithGasPrice } = useCallWithGasPrice();
    const token = pairToApprove ? pairToApprove.liquidityToken : undefined;
    const currentAllowance = useTokenAllowance(token, account !== null && account !== undefined ? account : undefined, spender);
    const pendingApproval = useHasPendingApproval(token === null || token === undefined ? undefined : token.address, spender);
    // check the current approval status
    const approvalState = useMemo(() => {
        if (!pairToApprove || !spender)
            return ApprovalState.UNKNOWN;
        if (pairToApprove.currency === ETHER)
            return ApprovalState.APPROVED;
        // we might not have enough data to know whether or not we need to approve
        if (!currentAllowance)
            return ApprovalState.UNKNOWN;
        // pairToApprove will be defined if currentAllowance is
        // eslint-disable-next-line no-nested-ternary
        return parseFloat(currentAllowance.toFixed(2)) <= 0
            ? pendingApproval
                ? ApprovalState.PENDING
                : ApprovalState.NOT_APPROVED
            : ApprovalState.APPROVED;
    }, [pairToApprove, currentAllowance, pendingApproval, spender]);
    const tokenContract = useTokenContract(token === null || token === undefined ? undefined : token.address);
    const addTransaction = useTransactionAdder();
    const approve = useCallback(async () => {
        if (approvalState !== ApprovalState.NOT_APPROVED) {
            console.error('approve was called unnecessarily');
            return;
        }
        if (!token) {
            console.error('no token');
            return;
        }
        if (!tokenContract) {
            console.error('tokenContract is null');
            return;
        }
        if (!pairToApprove) {
            console.error('missing amount to approve');
            return;
        }
        if (!spender) {
            console.error('no spender');
            return;
        }
        let useExact = false;
        const estimatedGas = await tokenContract.estimateGas.approve(spender, MaxUint256).catch(() => {
            // general fallback for tokens who restrict approval amounts
            useExact = true;
            return tokenContract.estimateGas.approve(spender, pairToApprove.raw.toString());
        });
        // eslint-disable-next-line consistent-return
        return callWithGasPrice(tokenContract, 'approve', [spender, useExact ? pairToApprove.raw.toString() : MaxUint256], {
            gasLimit: calculateGasMargin(estimatedGas),
        })
            .then((response) => {
                addTransaction(response, {
                    summary: `Approve ${pairToApprove.liquidityToken.symbol}`,
                    approval: { tokenAddress: token.address, spender },
                });
            })
            .catch((error) => {
                console.error('Failed to approve token', error);
                throw error;
            });
    }, [approvalState, token, tokenContract, pairToApprove, spender, addTransaction, callWithGasPrice]);
    return [approvalState, approve];
}
